import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CartServiceService {
  private itemsSubject = new BehaviorSubject<{ name: string, price: number }[]>([]);
  items$ = this.itemsSubject.asObservable();

  addItem(item: { name: string, price: number }) {
    const currentItems = this.itemsSubject.getValue();
    const updatedItems = [...currentItems, item];
    this.itemsSubject.next(updatedItems);
  }

  removeItem(item: { name: string, price: number }) {
    const currentItems = this.itemsSubject.getValue();
    const updatedItems = currentItems.filter(i => i !== item);
    this.itemsSubject.next(updatedItems);
  }

  updateItem(oldItem: { name: string, price: number }, newItem: { name: string, price: number }) {
    const currentItems = this.itemsSubject.getValue();
    const updatedItems = currentItems.map(item => (item === oldItem ? newItem : item));
    this.itemsSubject.next(updatedItems);
  }
  
}
