import { Component, OnInit } from '@angular/core';
import { CartServiceService } from '../cart-service.service';

@Component({
  selector: 'app-shopping-cart',
  templateUrl: './shopping-cart.component.html',
  styleUrls: ['./shopping-cart.component.css']
})
export class ShoppingCartComponent implements OnInit {

  constructor(private cartService: CartServiceService) {
    this.cartService.items$.subscribe(items => {
      this.items = items;
    });
  }


  ngOnInit(){
  }

  items: { name: string, price: number }[] = [];
  showAddForm = false;
  showEditForm = false;
  selectedIndex = -1;

  addItem(item: { name: string, price: number }) {
    this.items.push(item);
    this.showAddForm = false;
  }

  removeItem(item: { name: string, price: number }) {
    const index = this.items.indexOf(item);
    if (index >= 0) {
      this.items.splice(index, 1);
    }
  }

  editItem(index: number) {
    this.showEditForm = true;
    this.selectedIndex = index;
  }

  // updateItem(item: { name: string, price: number }) {
  //   if (this.selectedIndex >= 0 && this.selectedIndex < this.items.length) {
  //     this.items[this.selectedIndex] = item;
  //     this.showEditForm = false;
  //     this.selectedIndex = -1;

  
  //   }
  // }
  updateItem(item: { name: string, price: number }) {
    console.log("Updating item:", item);
    if (this.selectedIndex >= 0 && this.selectedIndex < this.items.length) {
      this.items[this.selectedIndex] = item;
      console.log("Updated items array:", this.items);
      this.showEditForm = false;
      this.selectedIndex = -1; // Reset selectedIndex after updating
    }
  }
  

}
