import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CartServiceService } from '../cart-service.service';

@Component({
  selector: 'app-add-item-form',
  templateUrl: './add-item-form.component.html',
  styleUrls: ['./add-item-form.component.css']
})
export class AddItemFormComponent implements OnInit {

  constructor(private cartService: CartServiceService) {}


  ngOnInit() {
  }
  @Input() editItem: { id: number,name: string, price: number } | undefined;
  @Output() addItem = new EventEmitter<{ name: string, price: number }>();

  itemName = '';
  itemPrice = 0;
  private idCounter = 1; 


  ngOnChanges() {
    if (this.editItem) {
      this.itemName = this.editItem.name;
      this.itemPrice = this.editItem.price;
    }
  }

  generateUniqueId() {
    return this.idCounter++; // Increment the counter for each new ID
  }


  submitForm() {
    if (this.itemName && this.itemPrice > 0) {
      const newItem = {id: this.generateUniqueId(), name: this.itemName, price: this.itemPrice };
      this.cartService.addItem(newItem);
      this.itemName = '';
      this.itemPrice = 0;
    }
  }
  
}
